"use client"

import { useState } from 'react'
import { Button } from '@/components/ui/button'
import { Textarea } from '@/components/ui/textarea'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Alert, AlertDescription } from '@/components/ui/alert'
import { Badge } from '@/components/ui/badge'
import { Separator } from '@/components/ui/separator'

interface SummaryResponse {
  bluf: string
  summary: string
  keyPoints: string[]
  originalLength: number
  summaryLength: number
  reductionPercentage: number
}

export default function HomePage() {
  const [inputText, setInputText] = useState('')
  const [result, setResult] = useState<SummaryResponse | null>(null)
  const [loading, setLoading] = useState(false)
  const [error, setError] = useState('')

  const handleSummarize = async () => {
    if (!inputText.trim()) {
      setError('Por favor, insira um texto para processar.')
      return
    }

    if (inputText.trim().length < 100) {
      setError('O texto deve ter pelo menos 100 caracteres para uma redu√ß√£o eficaz.')
      return
    }

    setLoading(true)
    setError('')
    setResult(null)

    try {
      const response = await fetch('/api/summarize', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ text: inputText }),
      })

      if (!response.ok) {
        throw new Error('Erro ao processar a solicita√ß√£o')
      }

      const data = await response.json()
      setResult(data)
    } catch (err) {
      setError('Falha ao processar o texto. Tente novamente.')
      console.error('Erro:', err)
    } finally {
      setLoading(false)
    }
  }

  const clearAll = () => {
    setInputText('')
    setResult(null)
    setError('')
  }

  return (
    <div className="container mx-auto px-4 py-8 max-w-4xl">
      {/* Header */}
      <div className="text-center mb-8">
        <h1 className="text-4xl font-bold mb-4">
          Agente de Redu√ß√£o Estrutural de Textos
        </h1>
        <p className="text-muted-foreground text-lg">
          Sistema h√≠brido para s√≠ntese inteligente de textos (5-15% do original)
        </p>
        <div className="flex justify-center gap-2 mt-4">
          <Badge variant="secondary">Grok4 Integration</Badge>
          <Badge variant="secondary">Gemini 2.5 Pro</Badge>
          <Badge variant="secondary">GPT-5 Precision</Badge>
        </div>
      </div>

      {/* Input Section */}
      <Card className="mb-6">
        <CardHeader>
          <CardTitle>Entrada de Texto</CardTitle>
          <CardDescription>
            Cole ou digite o texto que deseja reduzir estruturalmente
          </CardDescription>
        </CardHeader>
        <CardContent className="space-y-4">
          <Textarea
            placeholder="Cole seu texto aqui... (m√≠nimo 100 caracteres)"
            value={inputText}
            onChange={(e) => setInputText(e.target.value)}
            className="min-h-[200px] resize-y"
          />
          <div className="flex justify-between items-center">
            <span className="text-sm text-muted-foreground">
              {inputText.length} caracteres
            </span>
            <div className="space-x-2">
              <Button variant="outline" onClick={clearAll}>
                Limpar
              </Button>
              <Button 
                onClick={handleSummarize} 
                disabled={loading || !inputText.trim()}
              >
                {loading ? 'Processando...' : 'Reduzir Texto'}
              </Button>
            </div>
          </div>
        </CardContent>
      </Card>

      {/* Error Display */}
      {error && (
        <Alert className="mb-6 border-destructive">
          <AlertDescription className="text-destructive">
            {error}
          </AlertDescription>
        </Alert>
      )}

      {/* Results Section */}
      {result && (
        <div className="space-y-6">
          {/* Statistics */}
          <Card>
            <CardHeader>
              <CardTitle className="text-lg">Estat√≠sticas de Redu√ß√£o</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
                <div className="text-center">
                  <div className="text-2xl font-bold text-primary">
                    {result.originalLength}
                  </div>
                  <div className="text-sm text-muted-foreground">
                    Caracteres Originais
                  </div>
                </div>
                <div className="text-center">
                  <div className="text-2xl font-bold text-primary">
                    {result.summaryLength}
                  </div>
                  <div className="text-sm text-muted-foreground">
                    Caracteres Resumo
                  </div>
                </div>
                <div className="text-center">
                  <div className="text-2xl font-bold text-green-600">
                    {result.reductionPercentage}%
                  </div>
                  <div className="text-sm text-muted-foreground">
                    Redu√ß√£o Alcan√ßada
                  </div>
                </div>
              </div>
            </CardContent>
          </Card>

          {/* BLUF */}
          <Card>
            <CardHeader>
              <CardTitle className="text-lg">BLUF (Bottom Line Up Front)</CardTitle>
              <CardDescription>
                Conclus√£o direta na primeira linha
              </CardDescription>
            </CardHeader>
            <CardContent>
              <p className="text-lg font-medium leading-relaxed">
                {result.bluf}
              </p>
            </CardContent>
          </Card>

          {/* Summary */}
          <Card>
            <CardHeader>
              <CardTitle className="text-lg">Resumo Estrutural</CardTitle>
              <CardDescription>
                S√≠ntese coesa mantendo contexto e nuances cr√≠ticas
              </CardDescription>
            </CardHeader>
            <CardContent>
              <div className="prose prose-sm max-w-none">
                {result.summary.split('\n\n').map((paragraph, index) => (
                  <p key={index} className="mb-4 leading-relaxed">
                    {paragraph}
                  </p>
                ))}
              </div>
            </CardContent>
          </Card>

          {/* Key Points */}
          {result.keyPoints && result.keyPoints.length > 0 && (
            <Card>
              <CardHeader>
                <CardTitle className="text-lg">Pontos-Chave</CardTitle>
                <CardDescription>
                  Elementos essenciais extra√≠dos do texto
                </CardDescription>
              </CardHeader>
              <CardContent>
                <ul className="space-y-2">
                  {result.keyPoints.map((point, index) => (
                    <li key={index} className="flex items-start">
                      <span className="inline-block w-2 h-2 bg-primary rounded-full mt-2 mr-3 flex-shrink-0"></span>
                      <span className="leading-relaxed">{point}</span>
                    </li>
                  ))}
                </ul>
              </CardContent>
            </Card>
          )}
        </div>
      )}

      {/* Footer */}
      <div className="mt-12 text-center text-sm text-muted-foreground">
        <Separator className="mb-4" />
        <p>
          Pipeline: Percep√ß√£o ‚Üí Racioc√≠nio Multi-Agente ‚Üí Decanta√ß√£o ‚Üí Escultura ‚Üí Valida√ß√£o ‚Üí S√≠ntese Final
        </p>
      </div>
    </div>
  )
}
